* {
  font-family: Helvetica, sans-serif;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgb(256,256,256);
}

header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  background-color: #0c2517;
  height: 10vh;
  color: white;
  font-size: 40px;
}

/*Container for the entire body minus header and footer*/
#container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  height: 85vh;
}

/*Container for all mediaContainers*/
#innerContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 85vh;
}

#combinationMenu{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  background-color: rgb(220, 220, 220);
  height: 7vh;
}

.combinationButton{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 25%;
  background-color: rgb(0,0,0);
  height: 100%;
  padding:0;
  border: none;
}

.combinationButton:hover{
  background-color: rgb(256,256,256);
}

.combinationButton:hover .comboTextLarge{
  color: rgb(0,0,0);
}

.combinationButton:hover .comboTextSmall{
  color: rgb(0,0,0);
}

.comboTextLarge{
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.comboTextLargeSelected{
  font-size: 15px;
  font-weight: bold;
}

.comboTextSmall{
  display: none;
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.comboTextSmallSelected{
  display: none;
  font-size: 15px;
  font-weight: bold;
}

.combinationButtonSelected{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width:25%;
  background-color: rgb(220, 220, 220);
  height: 100%;
  padding:0;
  border:none;
}

#mediaContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 78vh;
  background-color: rgb(256,256,256);
}

#mediaPictureContainer{
  width:100%;
  height: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgb(220, 220, 220);
  border-bottom: 4px solid rgb(170, 170, 170);
}

#picFrame{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgb(256, 256, 256);
  height:85%;
  width:100%;
  overflow: hidden;
}

#svgImage{
  min-width: 20%;
  overflow: hidden;
}

#mediaAudioContainer{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 10%;
  background-color: white;
  padding:5px;
}

#mediaTextContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
  width: 100%;
  height: 20%;
  border-top: 4px solid rgb(170, 170, 170);
}

#mediaText{
  font-size: 17px;
  color: black;
  text-align: center;
}

#mediaCategory{
  width: 15%;
  height: 85vh;
    background-color: rgb(170, 170, 170);
}

#categoryHeader{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width:100%;
  height: 7vh;
  background-color: rgb(20, 20, 20);
  color: white;
  font-size: 20px;
}

#categoryButton{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width:100%;
  height: 7vh;
  background-color: #0c2517;
  color: white;
  font-size: 20px;
  margin-top: 5px;
  padding:0;
  border:none;
}

#Bilder, #Lyd, #Tekst{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width:auto;
  height: 18vh;
}

.categoryChoose{
  width: 100%;
  height: 100%;
  background-color: rgb(220, 220, 220);
  border:none;
  font-size: 17px;
  margin-top: 3px;

}

.categoryChoose:hover{
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
    background-color: rgb(240, 240, 240);

}

footer{
  margin-top:0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 5vh;
  width: 100%;
  background-color: #0c2517;
}

footer > a {
  text-decoration: none;
  color: white;
}

/*----------------------------------------------------*/
/*Responsivt design for mobile enheter*/

@media screen and (orientation: portrait){

  body{
    overflow-x: hidden;
  }

  #container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: auto;
  }

  #mediaContainer {
    width: 100%;
  }

  #combinationButton {
    width: 25%;
  }

  /* hides "Kombinasjon + tabnumber " */
  .comboTextLarge, .comboTextLargeSelected {
    display: none;
  }

  /* displays "tabnumber" instead */
  .comboTextSmall, .comboTextSmallSelected {
    display: inline;
  }

  #mediaTextContainer {
    width: 80%;
  }

  #mediaText{
    font-size: 15px;
  }

  #mediaCategory {
    width: 100%;
    min-height:10vh;
  }

  footer {
    display: none;
  }
}

@media only screen and (max-width: 1000px) {
  #svgImage{
    min-width: 30%;
  }
}

@media only screen and (max-width: 600px) {
  #svgImage{
    min-width: 40%;
  }
}

@media only screen and (max-width: 400px) {
  #svgImage{
    min-width: 55%;
  }
}

@media only screen and (max-width: 250px) {
  #svgImage{
    min-width: 65%;
  }
}

/*-----------------------------------------------------*/


